<template>
	<div id="index-copy">
		<public-header title="我的代金券"></public-header>
		<div class="content">
			<div class="coupon-list">
				<router-link to="" class="coupon-item" v-for="(item,index) in 4" :key="index">
					<div class="coupon-item-top">
						<div class="coupon-item-left">
							<div class="seller-img">
								<img src="http://p0.meituan.net/waimaipoi/08007a2a6508882b5517bbfb92fc02da499208.jpg.webp" alt="">
							</div>
							<div class="seller">
								<p class="seller-name">喵喵曰水果捞</p>
								<div class="seller-type">同享券</div>
								<div class="coupon-expires">有效期至:2018-7-31</div>
							</div>
						</div>
						<div class="coupon-item-right">
							<p class="coupon-money">
								<span class="money-unit">￥</span>
								2
							</p>
							<p class="coupon-condition">满30可用</p>
						</div>
					</div>
					<div class="coupon-item-line">
						<div class="line"></div>
					</div>
					<div class="coupon-item-bottom">
						<div class="explain-text">可与其他优惠活动同时享受。在线支付专享</div>
						<div class="to-use">
							去使用
							<van-icon name='right'></van-icon>
						</div>
					</div>
				</router-link>
				<div class="j-coupon-tip">
					没有更多代金券了&nbsp;&nbsp;|&nbsp;&nbsp;<router-link to="">查看过期券 &gt;&gt;</router-link>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				
			}
		},
		components: {
			PublicHeader
		}
	}
</script>

<style>
	#index-copy .content{
		position: fixed;
		top: 46px;
		right: 0;
		left: 0;
		bottom: 0;
		width: 100%;
		background: #f5f5f5;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#index-copy .content::-webkit-scrollbar {
		display: none;
	}
	#index-copy .content .coupon-list{
		display: block;
	}
	#index-copy .content .coupon-list .coupon-item{
		display: block;
		position: relative;
		margin: 10px;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top{
		display: -webkit-box;
		padding: 15px 15px;
		position: relative;
		background: #fff;
		overflow: hidden;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-left{
		position: relative;
		float: left;
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		width: 70%;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-left .seller-img{
		float: left;
		width: 55px;
		height: 55px;
		border: 1px solid #E4E4E4;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-left .seller-img img{
		width: 100%;
		height: 100%;
		border: none;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-left .seller{
		display: block;
		float: left;
		width: 65%;
		margin-left: 10px;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-left .seller .seller-name{
		line-height: 16px;
		font-size: 16px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		color: #2f2f2f;
		padding-bottom: 3px;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-left .seller .seller-type{
		width: 46px;
		height: 21px;
		background: #fff;
		padding: 4px;
		font-size: 12px;
		color: #FFA511;
		border: 1px solid #FFA511;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-left .seller .coupon-expires{
		font-size: 11px;
		color: #666;
		line-height: 12px;
		padding-top: 3px;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-right{
		text-align: right;
		width: 30%;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-right .coupon-money{
		font-size: 30px;
		color: #FB4E44;
		line-height: 35px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-right .coupon-money .money-unit{
		font-size: 24px;
		margin-right: -10px;
	}
	#index-copy .content .coupon-list .coupon-item .coupon-item-top .coupon-item-right .coupon-condition{
		color: #666;
		font-size: 12px;
		line-height: 16px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	#index-copy .content .coupon-list .coupon-item-line{
		position: relative;
	}
	#index-copy .content .coupon-list .coupon-item-line .line{
		border-top: 1px dashed #E6E6E6;
	}
	#index-copy .content .coupon-list .coupon-item-line .line:before{
		content: '';
		position: absolute;
		top: -5px;
		left: -2px;
		z-index: 20;
		width: 5px;
		height: 10px;
		border-radius: 0 5px 5px 0;
		border: 1px solid #f6f6f6;
		background: #f6f6f6;
	}
	#index-copy .content .coupon-list .coupon-item-line .line:after{
		content: '';
		position: absolute;
		top: -5px;
		right: -1px;
		z-index: 20;
		width: 5px;
		height: 10px;
		border-radius: 5px 0 0 5px;
		border: 1px solid #f6f6f6;
		background: #f6f6f6;
	}
	#index-copy .content .coupon-list .coupon-item-bottom{
		padding: 5px 15px;
		position: relative;
		background: #fff;
		overflow: hidden;
	}
	#index-copy .content .coupon-list .coupon-item-bottom .explain-text{
		width: 80%;
		float: left;
		line-height: 18px;
		color: #B3B3B3;
		font-size: 12px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	#index-copy .content .coupon-list .coupon-item-bottom .to-use{
		width: 20%;
		float: right;
		line-height: 18px;
		color: #FF2D4B;
		font-size: 12px;
		text-align: right;
	}
	#index-copy .content .coupon-list .j-coupon-tip{
		margin: 20px 0;
		text-align: center;
		color: #666;
		line-height: 18px;
		font-size: 12px;
	}
	#index-copy .content .coupon-list .j-coupon-tip a{
		color: #666;
		line-height: 18px;
		font-size: 12px;
	}
</style>


